.card {
    margin: 10px;
    border: 0px solid black;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgb(0,0,0,0.75);
    transition: transform 100ms ease-in;
    cursor: pointer;
}
.card > img {
    object-fit: cover;
    min-width: 300px;
    min-height: 200px;
    /* width:400px; */
    height:400px;
    width: 100%;
}

.card:hover {
    transform: scale(1.07)
}

