    font-weight: 600;
}

.card_info > h4 {
    font-size: 14px; 
    font-weight: 300;
    margin-top: 8px;
    margin-bottom: 8px;
}
.banner {
    /* vh = view height and so banner will take up 50% of the viewing screen */
    height: 55vh;
