.header_center{
    display: flex;
    /* allows the input box to take up middle space */
    flex:1 1;
    align-items:center;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border-radius: 999px;
    border: 1px solid lightgray;
    /* margin-left:70px; */
}

/* this takes the border off of the original input that comes with the text box
input is a child of .header_center div */
.header_center > input {
    border:none;
    padding: 10px;
    /* gets rid of the ugly blue outline */
    outline-width: 0;
    width:150px;
}

.header_right {
    display: flex;
    align-items:center;
    justify-content:center;
    width: 10vw;
    
}

.header_expand {
    cursor: pointer;
}


